import axios from 'axios'
import env from '../config/env'
import albumCard from '../components/albumCard.vue'
export default{
  components: { albumCard },
    name:'IndexPage',
    data(){
        return{
            albums:[]
        }
    },
    created(){
        axios.get(`${env.endpoint}/albums`).then(response=>this.albums=response.data) 
         
    }
}

